    class Reader extends React.Component {
        constructor(props) {
            super(props);
        }
        
    // componentDidMount() { 
    //      getVisibleIndex() 
    //     }

        render() {
            return (
                <Panel id={this.props.id}>
                <header className="panelheader">
                    <div className="textpanel" onClick={() => this.props.this.goBack()}>
                    <img alt="" src="https://img.icons8.com/wired/64/000000/back.png"/> 
                    </div>
                    {/* <Icon28SettingsOutline className="icon_settings" onClick={() => this.props.this.setActiveModal('settings_reader')}/> */}
                </header>

                        <div className="hard">
            <div className="text">
                <div dangerouslySetInnerHTML={{__html: this.props.this.state.booktext}} />
            </div>
            </div>
            {this.props.this.state.is_rated === false ? 
                <Button  style={{backgroundColor: "#00cbbe"}} stretched onClick={() => this.props.this.setState({activeModal: "modals", commentforrezention_add: ""})}></Button>
                :null}
            </Panel>
            )
            }
        }
  
export default Reader;